import { Form, Select, Table, PageHeader, Col, Button, Progress, Row, Tooltip } from "antd";
import React, { useState, useEffect, useRef } from "react";
import UploadData from "../../../components/UploadData/index";
import { SCREEN_ITEMS_TYPES, DATE_FORMAT } from "constants/CommonConstants";
import { PlusCircleOutlined } from "@ant-design/icons";
import useIntlMessage from "hooks/useIntlMessage";
import { SaveOutlined } from "@ant-design/icons";

import Utils from "../../../utils/index";
import paycomService from "../../../services/paycomService";
import moment from "moment";
import PaycomSampleFile from "assets/data/PaycomeSampleFile.xlsx";
import { ACTUAL_PAYCOM_DATE, REQUIRED_PAYCOM_DATE_FORMAT } from "../../../constants/DateConstant";

const BAR_COLORS = ["#108ee9", "#87d068"];
const Paycom = () => {
  const { Option, OptGroup } = Select;
  const [progress, setProgress] = useState(0);
  const [showUploadModal, setShowUplaodModal] = useState(false);
  const [form] = Form.useForm();
  const [header, setHeader] = useState(null);
  const [tableData, setTableData] = useState([]);
  const [loading, setLoading] = useState(false);
  const getMessage = useIntlMessage();

  const validateSpecificField = async rowKey => {
    for (const x of header) {
      try {
        await form.validateFields([`${rowKey}-${x}`]);
      } catch (error) {
        console.log(error);
        return false; // Validation failed for at least one field
      }
    }
    return true;
  };
  const disableRows = rowKey => {
    const copiedData = [...tableData];
    const dataWithDisabledRows = copiedData.map(x => {
      if (x.body.records.key === rowKey) {
        let item = x;
        item.disabled = true;
        return item;
      } else {
        return x;
      }
    });
    setTableData(dataWithDisabledRows);
  };
  const Save = async rowKey => {
    const validated = await validateSpecificField(rowKey);
    if (validated) {
      const values = form.getFieldsValue();
      const filteredData = Object.fromEntries(
        Object.entries(values)
          .filter(([key]) => key.startsWith(`${rowKey}-`))
          .map(([key, value]) => [key.slice(rowKey.toString().length + 1), value]),
      );

      let convertedDate = moment(filteredData["Hire Date"]);
      filteredData["Hire Date"] = convertedDate;

      const response = await paycomService.updateSingleRecords(filteredData);

      if (response) {
        disableRows(rowKey);
      }
    }
  };

  const columnsNew =
    header &&
    header.map((col, index) => {
      return {
        title: col,
        key: col,
        dataIndex: col,

        render: (text, record) => {
          const attribute = record?.body?.records?.attributeDtoList?.find(attr => attr.header === col);
          const options = attribute ? [attribute.payComValue, attribute.myTeamValue] : [];
          if (attribute?.sync) {
            return (
              <Form.Item name={`${record.body.records.key}-${col}`} initialValue={attribute.myTeamValue}>
                <span> {attribute.myTeamValue}</span>
              </Form.Item>
            );
          } else {
            return (
              <>
                <Form.Item
                  name={`${record.body.records.key}-${col}`}
                  rules={[{ required: true, message: `please enter ${col}` }]}
                >
                  <Select disabled={record.disabled}>
                    <OptGroup label={"Paycom"}>
                      <Option value={options[0]}>{options[0]}</Option>
                    </OptGroup>
                    <OptGroup label={"MyTeam"}>
                      <Option value={options[1]}>{options[1]}</Option>
                    </OptGroup>
                  </Select>
                </Form.Item>
              </>
            );
          }
        },
      };
    });

  let ColumnsForTable = columnsNew && [
    ...columnsNew,
    {
      title: "actions",
      dataIndex: "actions",
      key: "actions",
      render: (text, record) => (
        <span className="center">
          <Button onClick={() => Save(record.body.records.key)} disabled={record.disabled}>
            <Tooltip title={"Save"} placement="top">
              <SaveOutlined />
            </Tooltip>
          </Button>
        </span>
      ),
    },
  ];

  const handleUploadCancel = e => {
    setShowUplaodModal(false);
  };
  const handleUploadModal = () => {
    setShowUplaodModal(true);
    setProgress(0);
    setTableData([]);
    setHeader(null);
  };
  const getResponse = response => {
    try {
      if (!Utils.isEmpty(response)) {
        setLoading(true);
        const reader = response.body.getReader();
        const decoder = new TextDecoder("utf-8");
        let buffer = "";
        let rows = [];
        let totalLines = 0;
        let totalRecords = 0;
        let rowsTobeAdded = [];
        const processResult = result => {
          buffer += decoder.decode(result.value || new Uint8Array(), { stream: !result.done });
          const lines = buffer.split("\n");
          buffer = lines.pop();
          totalLines += lines.length;
          for (const line of lines) {
            const data = JSON.parse(line);
            rows.push(data);
            if (rows.length >= 1) {
              setHeader(rows[0].body.records.columns);
              totalRecords = rows[0].body.records.recordsCount;
              if (!data.body.records.recordIsSync && data.body.records.attributeDtoList?.length !== 0) {
                rowsTobeAdded.push(data);
                setTableData(rowsTobeAdded);
              }
            }
          }
          if (result.done) {
            setLoading(false);
            return;
          }
          increaseProgress(totalLines, totalRecords);
          return reader.read().then(processResult);
        };
        return reader.read().then(processResult);
      }
    } catch (error) {
      console.error("Something went wrong while uploading document", error);
    }
  };

  const increaseProgress = (lines, totalRecords) => {
    const progress = Math.floor((lines / totalRecords) * 100);
    setProgress(progress);
  };

  return (
    <>
      {showUploadModal && (
        <UploadData
          visible={showUploadModal}
          onCancel={handleUploadCancel}
          onResponse={getResponse}
          screen={SCREEN_ITEMS_TYPES.paycomSync}
          sampleFile={PaycomSampleFile}
        />
      )}
      <PageHeader className="p-2 pl-2" title={getMessage("component.paycom.header")} />
      <Col className="mb-2 text-xl-right text-md-right " xs={24} md={12} lg={24} xl={24}>
        <Button type="primary" onClick={handleUploadModal} id="manage-emp-upload-btn">
          <PlusCircleOutlined className="mr-2" id="manage-emp-upload-btn-icon" />
          {"upload file"}
        </Button>
      </Col>
      <Row className="mb-4">
        <Progress
          className="mt-5 mb-2"
          strokeColor={{
            "0%": BAR_COLORS[0],
            "100%": BAR_COLORS[1],
          }}
          percent={progress}
        />
      </Row>
      <Form form={form}>
        <Table
          columns={ColumnsForTable}
          dataSource={tableData.slice(1)}
          rowKey={record => record.body.records.key}
          loading={loading}
        />
      </Form>
    </>
  );
};
export default Paycom;
